#navbar {
  overflow: hidden;
  padding: 12px; /* Large padding which will shrink on scroll (using JS) */
  transition: 0.4s; /* Adds a transition effect when the padding is decreased */
  width: 100%;
  top: 0; /* At the top */
  position: relative;
}

/* Style the navbar links */
#navbar-right a {
  float: left;
  margin: 0px 4px;
  color: #ab8742;
  text-align: center;
  padding: 8px 12px;
  text-decoration: none;
  font-size: 36px;
  line-height: 25px;
  border-radius: 4px;
  font-family: "Franklin Gothic Medium" ;
}

/* Style the logo */
.link-logo {
  text-decoration: none;
  color: #ab8742;
  font-size: 36px;
  text-align: center;
  font-weight: bold;
  font-family: 'Comic Sans MS';
  z-index: 9999;
  display: inline;
  margin-right: 20px;
}

/* Links on mouse-over */
#navbar-right a:hover {
  background-color: #ddd;
  color: black;
}

/* Style the active/current link */
#navbar-right a.active {
  background-color: #ddd;
  color: white;
}

/* Display some links to the right */
#navbar-right {
  position: absolute;
  right: 210px;
  top: 16px;
  display: inline;
  display: initial;
}
.fixed-wallet {
  position: absolute;
  width: 190px;
  right: 5%;
  top: 20px;
  text-align: -webkit-center;
}
/* Add responsiveness - on screens less than 580px wide, display the navbar vertically instead of horizontally */
@media screen and (max-width: 1024px) {
  .fixed-wallet {
    position: absolute;
    right: 32px;
  }
}

@media screen and (max-width: 1023px) {

  .fixed-wallet {
    position: absolute;
    top: 80px;
  }
  #navbar-right a {
    margin: 0px 8px;
  }
  /* .fixed-wallet {
    position: absolute;
    margin: auto;
    width: 100%;
    top: 66px;
  } */
}

@media screen and (max-width: 660px) {
  .fixed-wallet {
    position: absolute;
    right: 0px;
    top: 20px;
  }
    #navbar-right {
    position: absolute;
    right: 32px;
  }
}


